$anim-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
$anim-duration: 250ms;

$button-background-color: #3f51b5;
$button-active-background-color: #303f9f;
$button-color: #ffffff;

$accent-color: #2196f3;
$color: #2a363b;

$border: 1px solid $color;
$border-movie: 1px solid rgb(42, 157, 246);

$box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
  0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
$box-shadow-movie: 0px 3px 18px 2px rgba(42, 157, 246, 0.66);

$text-color: #000000;
